<template>
  <div class="task-list">
    <el-card>
      <template #header>
        <div class="card-header">
          <h3></h3>
          <el-button type="primary" size="small" @click="refreshTasks"></el-button>
        </div>
      </template>
      
      <el-table :data="tasks" stripe style="width: 100%" v-loading="loading">
        <el-table-column prop="id" label="ID" width="60" />
        <el-table-column prop="city" label="" width="100" />
        <el-table-column prop="status" label="" width="100">
          <template #default="scope">
            <el-tag :type="getStatusType(scope.row.status)">{{ scope.row.status }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="start_time" label="" width="180">
          <template #default="scope">
            {{ formatDate(scope.row.start_time) }}
          </template>
        </el-table-column>
        <el-table-column prop="end_time" label="" width="180">
          <template #default="scope">
            {{ scope.row.end_time ? formatDate(scope.row.end_time) : '-' }}
          </template>
        </el-table-column>
        <el-table-column prop="total_items" label="" width="100" />
        <el-table-column prop="success_items" label="" width="100" />
        <el-table-column label="" width="180">
          <template #default="scope">
            <el-progress
              :percentage="calculateProgress(scope.row)"
              :status="getProgressStatus(scope.row.status)"
            />
          </template>
        </el-table-column>
        <el-table-column label="">
          <template #default="scope">
            <el-button 
              size="small"
              type="primary"
              @click="viewDetail(scope.row)"
              :disabled="scope.row.status === '' || scope.row.status === 'In Progress'"
            >
              
            </el-button>
            <el-button 
              size="small"
              type="success"
              @click="runAnalysis(scope.row)"
              :disabled="scope.row.status === '' || scope.row.status === 'In Progress' || scope.row.success_items === 0"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      
      <div class="pagination-container">
        <el-pagination
          background
          layout="total, sizes, prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="currentPage"
          :page-sizes="[10, 20, 50, 100]"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </el-card>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { ElMessage } from 'element-plus';
import api from '../api/api';
import { useRouter } from 'vue-router';

export default {
  name: 'TaskList',
  
  setup() {
    const router = useRouter();
    
    // 
    const tasks = ref([]);
    const loading = ref(false);
    
    // 
    const currentPage = ref(1);
    const pageSize = ref(10);
    const total = ref(0);
    
    // 
    const fetchTasks = async () => {
      try {
        loading.value = true;
        
        const params = {
          limit: pageSize.value,
          offset: (currentPage.value - 1) * pageSize.value
        };
        
        const result = await api.getTasks(params);
        tasks.value = result;
        
        // TODO: API
        total.value = 100; // API
        
        loading.value = false;
      } catch (error) {
        loading.value = false;
        ElMessage.error('');
        console.error(error);
      }
    };
    
    // 
    const refreshTasks = () => {
      fetchTasks();
    };
    
    // 
    const handleCurrentChange = (page) => {
      currentPage.value = page;
      fetchTasks();
    };
    
    // 
    const handleSizeChange = (size) => {
      pageSize.value = size;
      currentPage.value = 1;
      fetchTasks();
    };
    
    // 
    const viewDetail = (task) => {
      // 
      if (task.status === '' || task.status === 'In Progress') {
        ElMessage.warning('');
        return;
      }
      router.push({ name: 'houseList', query: { taskId: task.id, city: task.city } });
    };
    
    // 
    const runAnalysis = async (task) => {
      try {
        // 
        if (task.status === '' || task.status === 'In Progress') {
          ElMessage.warning('');
          return;
        }
        
        // 
        if (task.success_items === 0) {
          ElMessage.warning('');
          return;
        }
        
        loading.value = true;
        
        const data = {
          task_id: task.id,
          city: task.city,
          analysis_types: [] // 
        };
        
        await api.runAnalysis(data);
        
        loading.value = false;
        ElMessage.success('');
        
        // 
        router.push({ name: 'analysis', query: { city: task.city } });
      } catch (error) {
        loading.value = false;
        ElMessage.error('');
        console.error(error);
      }
    };
    
    // 
    const calculateProgress = (task) => {
      if (!task.total_items || task.total_items === 0) return 0;
      
      const progress = (task.success_items / task.total_items) * 100;
      return Math.min(Math.round(progress), 100);
    };
    
    // 
    const getStatusType = (status) => {
      switch (status) {
        case '':
        case 'In Progress':
          return 'warning';
        case '':
        case 'Completed':
          return 'success';
        case '':
        case 'Failed':
          return 'danger';
        default:
          return 'info';
      }
    };
    
    // 
    const getProgressStatus = (status) => {
      switch (status) {
        case '':
        case 'In Progress':
          return '';
        case '':
        case 'Completed':
          return 'success';
        case '':
        case 'Failed':
          return 'exception';
        default:
          return '';
      }
    };
    
    // 
    const formatDate = (dateString) => {
      if (!dateString) return '';
      
      const date = new Date(dateString);
      return date.toLocaleString();
    };
    
    // 
    onMounted(() => {
      fetchTasks();
    });
    
    return {
      tasks,
      loading,
      currentPage,
      pageSize,
      total,
      refreshTasks,
      handleCurrentChange,
      handleSizeChange,
      viewDetail,
      runAnalysis,
      calculateProgress,
      getStatusType,
      getProgressStatus,
      formatDate
    };
  }
};
</script>

<style scoped>
.task-list {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
</style> 