<template>
  <div class="captcha-control">
    <el-container>
      <el-header>
        <div class="header">
          <h1></h1>
          <el-button type="primary" @click="backToList"></el-button>
        </div>
      </el-header>
      
      <el-main>
        <div class="instruction-box" v-if="!sessionCompleted">
          <h3></h3>
          <p>1. </p>
          <p>2. </p>
          <p>3. ""</p>
          <p>4. ""</p>
        </div>
        
        <el-alert
          v-if="sessionCompleted"
          title=""
          type="success"
          description=""
          :closable="false"
          show-icon>
        </el-alert>
        
        <div class="session-info">
          <el-descriptions title="" :column="3" border>
            <el-descriptions-item label="ID">{{ sessionId }}</el-descriptions-item>
            <el-descriptions-item label="">
              <el-tag v-if="sessionStatus === 'active'" type="success"></el-tag>
              <el-tag v-else-if="sessionStatus === 'completed'" type="info"></el-tag>
              <el-tag v-else-if="sessionStatus === 'closed'" type="danger"></el-tag>
              <el-tag v-else type="warning">{{ sessionStatus }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="">{{ formatTime(sessionCreatedAt) }}</el-descriptions-item>
          </el-descriptions>
        </div>
        
        <div class="browser-container" ref="browserContainer">
          <div v-if="loading" class="loading-overlay">
            <div class="el-loading-spinner">
              <svg class="circular" viewBox="25 25 50 50">
                <circle class="path" cx="50" cy="50" r="20" fill="none"/>
              </svg>
              <p class="el-loading-text">...</p>
            </div>
          </div>
          
          <div class="click-indicator" v-if="showClickIndicator" :style="indicatorStyle"></div>
          
          <div v-if="!screenshot" class="no-screenshot">
            <span>...""</span>
          </div>
          
          <img 
            v-if="screenshot" 
            :src="screenshot" 
            alt="" 
            class="browser-screen"
          />
          
          <div 
            v-if="!sessionCompleted" 
            class="browser-overlay" 
            @click="handleScreenClick" 
            @mousemove="handleMouseMove"
            @mousedown="handleMouseDown"
            @mouseup="handleMouseUp"
            ref="browserOverlay">
          </div>
          
          <!--  -->
          <div class="floating-toolbar" v-if="screenshot" 
            :style="{top: toolbarPosition.y + 'px', left: toolbarPosition.x + 'px'}" 
            v-show="isToolbarVisible">
            <div class="toolbar-button" title="" 
              :class="{ active: interactionMode === 'click' }"
              @click="setInteractionMode('click')">
              <el-icon><Pointer /></el-icon>
            </div>
            <div class="toolbar-button" title="" 
              :class="{ active: interactionMode === 'drag' }"
              @click="setInteractionMode('drag')">
              <el-icon><DArrowRight /></el-icon>
            </div>
            <div class="toolbar-button" title="" @click="zoomIn">
              <el-icon><ZoomIn /></el-icon>
            </div>
          </div>
        </div>
        
        <!--  -->
        <div class="ai-suggestion" v-if="aiSuggestion">
          <el-icon><InfoFilled /></el-icon> <strong>:</strong> {{ aiSuggestion }}
        </div>
        
        <!--  -->
        <!-- <div class="direct-image" v-if="!screenshot">
          <p>:</p>
          <img :src="'/captcha/screenshots/session_' + sessionId + '.png?t=' + Date.now()" 
            alt="" style="max-width: 100%;" />
        </div> -->
        
        <div class="action-bar">
          <el-button 
            type="primary" 
            :disabled="sessionCompleted || loading" 
            @click="completeVerification">
            
          </el-button>
          
          <el-button 
            type="success" 
            :disabled="sessionCompleted || loading" 
            @click="initDragMode">
            
          </el-button>
          
          <el-button 
            type="info" 
            :disabled="sessionCompleted || loading" 
            @click="refreshBrowser">
            
          </el-button>
          
          <el-button 
            type="danger" 
            :disabled="sessionCompleted || loading" 
            @click="closeSession">
            
          </el-button>
        </div>
        
        <!--  -->
        <div class="shortcuts-panel" v-if="showShortcuts">
          <h4> <el-button type="text" size="small" @click="showShortcuts = false"></el-button></h4>
          <div><span class="shortcut-key">R</span> </div>
          <div><span class="shortcut-key">D</span> </div>
          <div><span class="shortcut-key">C</span> </div>
          <div><span class="shortcut-key">Esc</span> </div>
          <div><span class="shortcut-key">F1</span> /</div>
          <div><span class="shortcut-key">F2</span> /</div>
        </div>
        
        <!--  -->
        <div class="debug-info" v-if="showDebug">
          <p>WebSocket: {{ wsStatus }}</p>
          <p>: {{ lastMessage }}</p>
          <p>: {{ lastUpdateTime }}</p>
          <el-button size="small" @click="reconnectWebSocket">WebSocket</el-button>
          <el-button size="small" @click="showDebug = false"></el-button>
        </div>
        
        <!--  -->
        <div class="operation-log">
          <h3> <el-button type="text" @click="clearLogs"></el-button></h3>
          <div v-if="operationLogs.length === 0" class="log-entry log-info">
            
          </div>
          <div v-for="(log, index) in operationLogs" :key="index" 
               :class="['log-entry', 'log-' + log.type]">
            <span>{{ log.time }} - {{ log.message }}</span>
          </div>
        </div>

        <!--  -->
        <div class="drag-mode-indicator" v-if="isDragMode">
          <p v-if="isDragMode === true">
            <strong>:</strong> 
          </p>
          <p v-else-if="isDragMode === 'endpoint'">
            <strong>:</strong> 
          </p>
        </div>
      </el-main>
      
      <el-footer>
        <div style="text-align: center; margin-top: 20px; padding: 20px; border-top: 1px solid #eee;">
          <p> &copy; 2025</p>
        </div>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import { ref, reactive, computed, onMounted, onBeforeUnmount } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import axios from 'axios'
import { 
  Pointer, 
  DArrowRight, 
  ZoomIn, 
  InfoFilled
} from '@element-plus/icons-vue'
import { ElMessage } from 'element-plus'

export default {
  name: 'CaptchaControl',
  components: {
    Pointer,
    DArrowRight,
    ZoomIn,
    InfoFilled
  },
  setup() {
    // 
    const router = useRouter()
    const route = useRoute()
    
    // 
    const sessionId = ref(route.params.sessionId)
    const sessionStatus = ref('')
    const sessionCreatedAt = ref('')
    const sessionCompleted = ref(false)
    
    // UI 
    const screenshot = ref('')
    const loading = ref(true)
    const wsConnection = ref(null)
    const browserContainer = ref(null)
    const browserOverlay = ref(null)
    
    // 
    const isDragMode = ref(false)
    const dragStart = reactive({ x: 0, y: 0 })
    const dragEnd = reactive({ x: 0, y: 0 })
    
    // 
    const interactionMode = ref('click') // 'click', 'drag'
    
    // 
    const isToolbarVisible = ref(true)
    const toolbarPosition = reactive({ x: 10, y: 10 })
    
    // 
    const mousePosition = reactive({ x: 0, y: 0 })
    const isMouseDown = ref(false)
    
    // 
    const aiSuggestion = ref('')
    
    // 
    const wsStatus = ref('')
    const lastMessage = ref('')
    const lastUpdateTime = ref('')
    const showDebug = ref(false)
    const showShortcuts = ref(true)
    
    // 
    const operationLogs = ref([])
    const maxLogs = 20 // 20
    
    // 
    const showClickIndicator = ref(false)
    const indicatorPosition = reactive({ x: 0, y: 0 })
    
    const indicatorStyle = computed(() => ({
      left: `${indicatorPosition.x}px`,
      top: `${indicatorPosition.y}px`
    }))
    
    // 
    const formatTime = (timeStr) => {
      if (!timeStr) return ''
      try {
        return new Date(timeStr).toLocaleString()
      } catch (e) {
        return timeStr
      }
    }
    
    // WebSocket
    const connectWebSocket = () => {
      if (wsConnection.value && wsConnection.value.readyState !== WebSocket.CLOSED) {
        wsConnection.value.close()
      }
      
      wsStatus.value = '...'
      addLog('WebSocket...', 'info')
      const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:'
      const wsUrl = `${protocol}//${window.location.host}/captcha/ws/${sessionId.value}`
      
      addLog(`WebSocketURL: ${wsUrl}`, 'info')
      
      try {
        const ws = new WebSocket(wsUrl)
        
        ws.onopen = () => {
          addLog('WebSocket', 'success')
          wsStatus.value = ''
          
          // 
          ws.send("get_screenshot")
        }
        
        ws.onmessage = (event) => {
          try {
            const data = JSON.parse(event.data)
            lastMessage.value = JSON.stringify(data).substring(0, 50) + '...'
            
            if (data.type === 'screenshot_update' && data.screenshot) {
              screenshot.value = `data:image/png;base64,${data.screenshot}`
              loading.value = false
              lastUpdateTime.value = new Date().toLocaleTimeString()
              addLog('', 'info')
            } else if (data.type === 'status_update') {
              sessionStatus.value = data.status
              addLog(`: ${data.status}`, 'info')
              
              if (data.status === 'completed') {
                sessionCompleted.value = true
                ElMessage.success('')
              }
            } else if (data.type === 'error') {
              addLog(`: ${data.message}`, 'error')
              ElMessage.error(data.message)
            }
          } catch (error) {
            addLog(`WebSocket: ${error.message}`, 'error')
          }
        }
        
        ws.onerror = (error) => {
          addLog(`WebSocket: ${error}`, 'error')
          wsStatus.value = ''
        }
        
        ws.onclose = () => {
          addLog('WebSocket', 'warning')
          wsStatus.value = ''
          
          // 
          if (!sessionCompleted.value) {
            setTimeout(connectWebSocket, 3000)
          }
        }
        
        wsConnection.value = ws
      } catch (error) {
        addLog(`WebSocket: ${error.message}`, 'error')
        wsStatus.value = ''
      }
    }
    
    // WebSocket
    const reconnectWebSocket = () => {
      wsStatus.value = '...'
      connectWebSocket()
    }
    
    // 
    const fetchSessionInfo = async () => {
      try {
        const response = await axios.get(`/captcha/api/check_verification/${sessionId.value}`)
        console.log(':', response.data)
        const { verified, status } = response.data
        
        sessionStatus.value = status || (verified ? 'completed' : 'active')
        sessionCompleted.value = verified
        
        if (verified) {
          if (wsConnection.value) {
            wsConnection.value.close()
          }
        }
      } catch (error) {
        console.error(':', error)
      }
    }
    
    // 
    const loadDirectScreenshot = async () => {
      try {
        addLog('WebSocket...', 'info');
        // URLWebSocket
        // WebSocket
        if (!wsConnection.value || wsConnection.value.readyState !== WebSocket.OPEN) {
          connectWebSocket();
        } else {
          // 
          wsConnection.value.send('get_screenshot');
        }
        
        // 
        const timeout = setTimeout(() => {
          if (loading.value) {
            addLog('WebSocket', 'warning');
          }
        }, 5000);
        
        return () => clearTimeout(timeout);
      } catch (error) {
        console.error(':', error);
        addLog('""', 'error');
      }
    }
    
    // 
    const addLog = (message, type = 'info') => {
      const time = new Date().toLocaleTimeString()
      operationLogs.value.unshift({ time, message, type })
      
      // 
      if (operationLogs.value.length > maxLogs) {
        operationLogs.value = operationLogs.value.slice(0, maxLogs)
      }
      
      // 
      if (type === 'error') {
        suggestAction('')
      }
    }
    
    // 
    const clearLogs = () => {
      operationLogs.value = []
    }
    
    // 
    const handleScreenClick = async (event) => {
      if (loading.value || sessionCompleted.value) return
      
      const rect = browserOverlay.value.getBoundingClientRect()
      const x = event.clientX - rect.left
      const y = event.clientY - rect.top
      
      // 
      if (isDragMode.value === true) {
        // 
        dragStart.x = x
        dragStart.y = y
        
        // 
        addLog(`: (${Math.round(x)}, ${Math.round(y)})`, 'info')
        ElMessage.info('')
        
        // 
        isDragMode.value = 'endpoint'
        return
      }
      
      // 
      if (isDragMode.value === 'endpoint') {
        // 
        dragEnd.x = x
        dragEnd.y = y
        
        addLog(`: (${Math.round(x)}, ${Math.round(y)})`, 'info')
        
        // 
        await executeDrag()
        
        // 
        isDragMode.value = false
        return
      }
      
      // 
      indicatorPosition.x = x
      indicatorPosition.y = y
      showClickIndicator.value = true
      
      setTimeout(() => {
        showClickIndicator.value = false
      }, 500)
      
      loading.value = true
      addLog(`: (${Math.round(x)}, ${Math.round(y)})`, 'info')
      
      try {
        console.log(':', x, y)
        const response = await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'click',
          x: Math.round(x),
          y: Math.round(y)
        })
        
        // 
        if (response.data.success) {
          addLog(`: ${response.data.details?.method ? '' + response.data.details.method : ''}`, 'success')
        } else {
          addLog(`: ${response.data.details?.error || ''}`, 'error')
        }
      } catch (error) {
        console.error(':', error)
        addLog(`: ${error.message || ''}`, 'error')
      } finally {
        loading.value = false
      }
    }
    
    // 
    const handleMouseMove = (event) => {
      if (loading.value || sessionCompleted.value) return
      
      const rect = browserOverlay.value.getBoundingClientRect()
      mousePosition.x = event.clientX - rect.left
      mousePosition.y = event.clientY - rect.top
      
      // 
      if (isMouseDown.value && interactionMode.value === 'drag') {
        // 
      }
    }
    
    // 
    const handleMouseDown = (event) => {
      if (loading.value || sessionCompleted.value) return
      
      isMouseDown.value = true
      
      const rect = browserOverlay.value.getBoundingClientRect()
      const x = event.clientX - rect.left
      const y = event.clientY - rect.top
      
      if (interactionMode.value === 'drag') {
        dragStart.x = x
        dragStart.y = y
        addLog(`: (${Math.round(x)}, ${Math.round(y)})`, 'info')
      }
    }
    
    // 
    const handleMouseUp = async (event) => {
      if (loading.value || sessionCompleted.value) return
      
      if (!isMouseDown.value) return
      isMouseDown.value = false
      
      const rect = browserOverlay.value.getBoundingClientRect()
      const x = event.clientX - rect.left
      const y = event.clientY - rect.top
      
      // 
      if (interactionMode.value === 'drag') {
        // 
        const deltaX = x - dragStart.x
        const deltaY = y - dragStart.y
        
        if (Math.abs(deltaX) > 10 || Math.abs(deltaY) > 10) {
          dragEnd.x = x
          dragEnd.y = y
          addLog(`: (${Math.round(x)}, ${Math.round(y)})`, 'info')
          await executeDrag()
        }
      }
    }
    
    // 
    const setInteractionMode = (mode) => {
      interactionMode.value = mode
      isDragMode.value = (mode === 'drag')
      
      if (mode === 'drag') {
        addLog('', 'info')
        suggestAction('')
      } else {
        addLog('', 'info')
        suggestAction('')
      }
    }
    
    // 
    const zoomIn = () => {
      addLog('', 'info')
      suggestAction(' (Ctrl + +)')
    }
    
    // 
    const suggestAction = (suggestion) => {
      aiSuggestion.value = suggestion
      // 5
      setTimeout(() => {
        if (aiSuggestion.value === suggestion) {
          aiSuggestion.value = ''
        }
      }, 5000)
    }
    
    // 
    const executeDrag = async () => {
      loading.value = true
      addLog(`: (${Math.round(dragStart.x)}, ${Math.round(dragStart.y)})(${Math.round(dragEnd.x)}, ${Math.round(dragEnd.y)})`, 'info')
      
      try {
        console.log(`: (${dragStart.x}, ${dragStart.y})(${dragEnd.x}, ${dragEnd.y})`)
        const response = await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'drag',
          x: Math.round(dragStart.x),
          y: Math.round(dragStart.y),
          target_x: Math.round(dragEnd.x)
        })
        
        if (response.data.success) {
          addLog(`: ${response.data.details?.method ? '' + response.data.details.method : ''}`, 'success')
          ElMessage.success('')
        } else {
          addLog(`: ${response.data.details?.error || ''}`, 'error')
          ElMessage.error('')
        }
      } catch (error) {
        console.error(':', error)
        addLog(`: ${error.message || ''}`, 'error')
        ElMessage.error('')
      } finally {
        loading.value = false
      }
    }
    
    // 
    const initDragMode = () => {
      isDragMode.value = true
      addLog('', 'info')
      suggestAction('')
    }
    
    // 
    const refreshBrowser = async () => {
      if (loading.value || sessionCompleted.value) return
      
      loading.value = true
      addLog('', 'info')
      
      try {
        const response = await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'refresh'
        })
        
        if (response.data.success) {
          addLog('', 'success')
        } else {
          addLog(`: ${response.data.details?.error || ''}`, 'error')
        }
        
        console.log('')
      } catch (error) {
        console.error(':', error)
        addLog(`: ${error.message || ''}`, 'error')
      } finally {
        loading.value = false
      }
    }
    
    // 
    const completeVerification = async () => {
      if (loading.value || sessionCompleted.value) return
      
      loading.value = true
      addLog('', 'info')
      
      try {
        const response = await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'complete'
        })
        
        sessionCompleted.value = true
        sessionStatus.value = 'completed'
        
        if (response.data.success) {
          addLog('', 'success')
        }
        
        console.log('')
        
        if (wsConnection.value) {
          wsConnection.value.close()
        }
      } catch (error) {
        console.error(':', error)
        addLog(`: ${error.message || ''}`, 'error')
      } finally {
        loading.value = false
      }
    }
    
    // 
    const closeSession = async () => {
      if (loading.value) return
      
      loading.value = true
      
      try {
        await axios.post(`/captcha/api/session/${sessionId.value}/action`, {
          action_type: 'close'
        })
        
        sessionStatus.value = 'closed'
        
        console.log('')
        
        if (wsConnection.value) {
          wsConnection.value.close()
        }
        
        // 
        setTimeout(() => {
          router.push('/captcha')
        }, 1500)
      } catch (error) {
        console.error(':', error)
      } finally {
        loading.value = false
      }
    }
    
    // 
    const backToList = () => {
      router.push('/captcha')
    }
    
    // 
    onMounted(() => {
      console.log('')
      addLog('', 'info')
      fetchSessionInfo()
      connectWebSocket()
      // 
      loadDirectScreenshot()
      
      // 
      setTimeout(() => {
        if (screenshot.value) {
          analyzeScreenshot()
        }
      }, 2000)
      
      // 10
      const intervalId = setInterval(fetchSessionInfo, 10000)
      
      // 
      const handleKeyDown = (event) => {
        if (loading.value || sessionCompleted.value) return
        
        // Esc
        if (event.key === 'Escape') {
          if (isDragMode.value) {
            isDragMode.value = false
            interactionMode.value = 'click'
            addLog('', 'info')
          }
          aiSuggestion.value = ''
          return
        }
        
        // 
        if (event.key === 'F1') {
          // F1/
          showShortcuts.value = !showShortcuts.value
          return
        }
        
        if (event.key === 'F2' || event.key === 'd' || event.key === 'D') {
          // F2D/
          showDebug.value = !showDebug.value
          return
        }
        
        // 
        switch (event.key.toLowerCase()) {
          case 'r':
            // R
            refreshBrowser()
            break
          case 'd':
            // D
            initDragMode()
            break
          case 'c':
            // C
            completeVerification()
            break
          case 't':
            // T
            isToolbarVisible.value = !isToolbarVisible.value
            break
        }
      }
      
      window.addEventListener('keydown', handleKeyDown)
      
      // 
      const analyzeScreenshot = () => {
        // 
        suggestAction('F1')
      }
      
      onBeforeUnmount(() => {
        clearInterval(intervalId)
        if (wsConnection.value) {
          wsConnection.value.close()
        }
        window.removeEventListener('keydown', handleKeyDown)
      })
    })
    
    return {
      // 
      sessionId,
      sessionStatus,
      sessionCreatedAt,
      sessionCompleted,
      screenshot,
      loading,
      showClickIndicator,
      indicatorStyle,
      wsStatus,
      lastMessage,
      lastUpdateTime,
      isDragMode,
      showDebug,
      operationLogs,
      interactionMode,
      isToolbarVisible,
      toolbarPosition,
      showShortcuts,
      aiSuggestion,
      
      // 
      formatTime,
      handleScreenClick,
      refreshBrowser,
      completeVerification,
      closeSession,
      backToList,
      reconnectWebSocket,
      initDragMode,
      clearLogs,
      handleMouseMove,
      handleMouseDown,
      handleMouseUp,
      setInteractionMode,
      zoomIn,
      
      // 
      browserContainer,
      browserOverlay
    }
  }
}
</script>

<style scoped>
.captcha-control {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
  color: #333;
}

.header {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.browser-container {
  position: relative;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 20px;
  background-color: #fff;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  min-height: 400px; /*  */
}

.browser-screen {
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  display: block;
}

.browser-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: crosshair;
}

.action-bar {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin: 20px 0;
}

.session-info {
  background-color: #fff;
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 20px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.instruction-box {
  background-color: #ecf8ff;
  border-left: 5px solid #409eff;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 4px;
}

.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

