import { createRouter, createWebHistory } from 'vue-router'

// 
import Home from '../views/Home.vue'
import TaskList from '../views/TaskList.vue'
import HouseList from '../views/HouseList.vue'
import Analysis from '../views/Analysis.vue'
import NotFound from '../views/NotFound.vue'
// 
import CaptchaList from '../views/captcha/CaptchaList.vue'
import CaptchaControl from '../views/captcha/CaptchaControl.vue'
import CaptchaVerify from '../views/captcha/CaptchaVerify.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
    meta: {
      title: ' - '
    }
  },
  {
    path: '/tasks',
    name: 'taskList',
    component: TaskList,
    meta: {
      title: ' - '
    }
  },
  {
    path: '/houses',
    name: 'houseList',
    component: HouseList,
    meta: {
      title: ' - '
    }
  },
  {
    path: '/analysis',
    name: 'analysis',
    component: Analysis,
    meta: {
      title: ' - '
    }
  },
  // 
  {
    path: '/captcha',
    name: 'captchaList',
    component: CaptchaList,
    meta: {
      title: ' - '
    }
  },
  {
    path: '/captcha/control/:sessionId',
    name: 'captchaControl',
    component: CaptchaControl,
    meta: {
      title: ' - '
    }
  },
  {
    path: '/captcha/verify/:sessionId',
    name: 'captchaVerify',
    component: CaptchaVerify,
    meta: {
      title: ' - '
    }
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'notFound',
    component: NotFound,
    meta: {
      title: '404 Not Found - '
    }
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

// 
router.beforeEach((to, from, next) => {
  // 
  document.title = to.meta.title || ''
  next()
})

export default router 