import axios from 'axios';

// axios
const apiClient = axios.create({
  baseURL: '/api', // 
  timeout: 10000, // 
  headers: {
    'Content-Type': 'application/json',
  },
  withCredentials: false // 
});

// 
apiClient.interceptors.request.use(
  config => {
    // 
    return config;
  },
  error => {
    // 
    console.error(':', error);
    return Promise.reject(error);
  }
);

// 
apiClient.interceptors.response.use(
  response => {
    // 
    return response.data;
  },
  error => {
    // 
    console.error(':', error);
    return Promise.reject(error);
  }
);

// API
export default {
  // 
  getCities() {
    return apiClient.get('/cities');
  },
  
  // 
  createCrawlTask(data) {
    return apiClient.post('/tasks/crawl', data);
  },
  
  getTasks(params) {
    return apiClient.get('/tasks', { params });
  },
  
  getTaskById(taskId) {
    return apiClient.get(`/tasks/${taskId}`);
  },
  
  // 
  getHouses(params) {
    return apiClient.get('/houses', { params });
  },
  
  getHouseCount(params) {
    return apiClient.get('/houses/count', { params });
  },
  
  getHouseById(houseId) {
    return apiClient.get(`/houses/${houseId}`);
  },
  
  // 
  runAnalysis(data) {
    return apiClient.post('/analysis/run', data);
  },
  
  getAnalysisResults(params) {
    return apiClient.get('/analysis/results', { params });
  },
  
  getAnalysisResultById(resultId) {
    return apiClient.get(`/analysis/results/${resultId}`);
  },
  
  getAnalysisTypes() {
    return apiClient.get('/analysis/types');
  },
  
  // 
  getDistricts(city) {
    return apiClient.get('/districts', { params: { city } });
  },
  
  // 
  getSummaryStatistics(city) {
    return apiClient.get('/statistics/summary', { params: { city } });
  }
}; 