<template>
  <div class="house-list">
    <el-card>
      <template #header>
        <div class="card-header">
          <h3></h3>
          <div class="filter-actions">
            <el-button type="primary" size="small" @click="refreshData"></el-button>
            <el-button type="success" size="small" @click="exportData"></el-button>
          </div>
        </div>
      </template>
      
      <!--  -->
      <div class="filter-container">
        <el-form :model="filters" inline>
          <el-form-item label="">
            <el-select v-model="filters.city" placeholder="" clearable @change="handleCityChange">
              <el-option
                v-for="(code, name) in cities"
                :key="name"
                :label="name"
                :value="name"
              />
            </el-select>
          </el-form-item>
          
          <el-form-item label="">
            <el-select v-model="filters.district" placeholder="" clearable>
              <el-option
                v-for="district in districts"
                :key="district"
                :label="district"
                :value="district"
              />
            </el-select>
          </el-form-item>
          
          <el-form-item label="">
            <el-input-number v-model="filters.minPrice" placeholder="" :min="0" />
            <span class="separator">-</span>
            <el-input-number v-model="filters.maxPrice" placeholder="" :min="0" />
          </el-form-item>
          
          <el-form-item label="">
            <el-select v-model="filters.roomCount" placeholder="" clearable>
              <el-option v-for="i in 5" :key="i" :label="`${i}`" :value="i" />
            </el-select>
          </el-form-item>
          
          <el-form-item>
            <el-button type="primary" @click="applyFilters"></el-button>
            <el-button @click="resetFilters"></el-button>
          </el-form-item>
        </el-form>
      </div>
      
      <!--  -->
      <el-table :data="houses" stripe style="width: 100%" v-loading="loading">
        <el-table-column type="expand">
          <template #default="props">
            <div class="house-detail">
              <el-row :gutter="20">
                <el-col :span="8">
                  <img :src="props.row.image" alt="" class="house-image" />
                </el-col>
                <el-col :span="16">
                  <p><strong>:</strong> {{props.row.location_qu}} {{props.row.location_big}} {{props.row.location_small}}</p>
                  <p><strong>:</strong> {{props.row.room}}</p>
                  <p><strong>:</strong> {{props.row.size}}</p>
                  <p><strong>:</strong> {{props.row.unit_price}}//</p>
                  <p><strong>:</strong> {{props.row.direction}}</p>
                  <p><strong>:</strong> {{props.row.floor}}</p>
                  <p><strong>:</strong> {{formatDate(props.row.crawl_time)}}</p>
                  <el-button type="primary" size="small" @click="openLink(props.row.link)"></el-button>
                </el-col>
              </el-row>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="title" label="" min-width="250" />
        <el-table-column prop="price" label="(/)" width="120" sortable />
        <el-table-column prop="location_qu" label="" width="100" />
        <el-table-column prop="size" label="()" width="100" sortable />
        <el-table-column prop="room" label="" width="120" />
        <el-table-column prop="direction" label="" width="100" />
      </el-table>
      
      <!--  -->
      <div class="pagination-container">
        <el-pagination
          background
          layout="total, sizes, prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="currentPage"
          :page-sizes="[10, 20, 50, 100]"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </el-card>
  </div>
</template>

<script>
import { ref, reactive, onMounted, watch } from 'vue';
import { ElMessage } from 'element-plus';
import { useRoute } from 'vue-router';
import api from '../api/api';

export default {
  name: 'HouseList',
  
  setup() {
    const route = useRoute();
    
    // 
    const houses = ref([]);
    const loading = ref(false);
    
    // 
    const filters = reactive({
      city: '',
      district: '',
      minPrice: null,
      maxPrice: null,
      roomCount: null
    });
    
    // 
    const cities = ref({});
    const districts = ref([]);
    
    // 
    const currentPage = ref(1);
    const pageSize = ref(20);
    const total = ref(0);
    
    // 
    const fetchData = async () => {
      try {
        loading.value = true;
        
        // 
        const params = {
          city: filters.city || undefined,
          district: filters.district || undefined,
          min_price: filters.minPrice || undefined,
          max_price: filters.maxPrice || undefined,
          room_count: filters.roomCount || undefined,
          limit: pageSize.value,
          offset: (currentPage.value - 1) * pageSize.value
        };
        
        // 
        houses.value = await api.getHouses(params);
        
        // 
        const countResult = await api.getHouseCount(params);
        total.value = countResult.count;
        
        loading.value = false;
      } catch (error) {
        loading.value = false;
        ElMessage.error('');
        console.error(error);
      }
    };
    
    // 
    const getCities = async () => {
      try {
        cities.value = await api.getCities();
      } catch (error) {
        ElMessage.error('');
        console.error(error);
      }
    };
    
    // 
    const getDistricts = async (city) => {
      try {
        if (!city) {
          districts.value = [];
          return;
        }
        
        districts.value = await api.getDistricts(city);
      } catch (error) {
        ElMessage.error('');
        console.error(error);
      }
    };
    
    // 
    watch(() => route.query, (query) => {
      if (query.city) {
        filters.city = query.city;
      }
      if (query.taskId) {
        // ID
        // APIID
        fetchData();
      }
    }, { immediate: true });
    
    // 
    const handleCityChange = (city) => {
      filters.district = '';
      getDistricts(city);
    };
    
    // 
    const refreshData = () => {
      fetchData();
    };
    
    // 
    const applyFilters = () => {
      currentPage.value = 1;
      fetchData();
    };
    
    // 
    const resetFilters = () => {
      // 
      const city = filters.city;
      
      Object.assign(filters, {
        city,
        district: '',
        minPrice: null,
        maxPrice: null,
        roomCount: null
      });
      
      currentPage.value = 1;
      fetchData();
    };
    
    // 
    const exportData = () => {
      ElMessage.info('');
      // TODO: 
    };
    
    // 
    const openLink = (url) => {
      window.open(url, '_blank');
    };
    
    // 
    const handleCurrentChange = (page) => {
      currentPage.value = page;
      fetchData();
    };
    
    // 
    const handleSizeChange = (size) => {
      pageSize.value = size;
      currentPage.value = 1;
      fetchData();
    };
    
    // 
    const formatDate = (dateString) => {
      if (!dateString) return '';
      
      const date = new Date(dateString);
      return date.toLocaleString();
    };
    
    // 
    onMounted(() => {
      getCities();
      
      // URL
      if (route.query.city) {
        filters.city = route.query.city;
        getDistricts(filters.city);
      }
      
      fetchData();
    });
    
    return {
      houses,
      loading,
      filters,
      cities,
      districts,
      currentPage,
      pageSize,
      total,
      handleCityChange,
      refreshData,
      applyFilters,
      resetFilters,
      exportData,
      openLink,
      handleCurrentChange,
      handleSizeChange,
      formatDate
    };
  }
};
</script>

<style scoped>
.house-list {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter-container {
  margin-bottom: 20px;
}

.filter-actions {
  display: flex;
  gap: 10px;
}

.separator {
  margin: 0 5px;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.house-detail {
  padding: 10px;
}

.house-image {
  width: 100%;
  max-width: 240px;
  border-radius: 4px;
}
</style> 