 <template>
  <div class="home">
    <el-row :gutter="20">
      <el-col :span="24">
        <div class="welcome-section">
          <h1></h1>
          <p class="subtitle"></p>
        </div>
      </el-col>
    </el-row>

    <el-row :gutter="20">
      <el-col :span="16">
        <el-card class="intro-card">
          <template #header>
            <div class="card-header">
              <h2></h2>
            </div>
          </template>
          <div class="system-intro">
            <p>PythonPySpark</p>
            
            <h3>:</h3>
            <el-row :gutter="20">
              <el-col :span="8">
                <el-card shadow="hover" class="feature-card">
                  <template #header>
                    <div class="feature-header">
                      <el-icon><Download /></el-icon>
                      <span></span>
                    </div>
                  </template>
                  <p></p>
                </el-card>
              </el-col>
              
              <el-col :span="8">
                <el-card shadow="hover" class="feature-card">
                  <template #header>
                    <div class="feature-header">
                      <el-icon><DataAnalysis /></el-icon>
                      <span></span>
                    </div>
                  </template>
                  <p>PySpark</p>
                </el-card>
              </el-col>
              
              <el-col :span="8">
                <el-card shadow="hover" class="feature-card">
                  <template #header>
                    <div class="feature-header">
                      <el-icon><PieChart /></el-icon>
                      <span></span>
                    </div>
                  </template>
                  <p>ECharts</p>
                </el-card>
              </el-col>
            </el-row>
            
            <h3>:</h3>
            <ol>
              <li></li>
              <li><router-link to="/tasks"></router-link></li>
              <li>""""</li>
              <li><router-link to="/analysis"></router-link></li>
            </ol>
          </div>
        </el-card>
      </el-col>
      
      <el-col :span="8">
        <CrawlTaskForm @task-created="handleTaskCreated" />
        
        <el-card class="latest-tasks" v-loading="loading">
          <template #header>
            <div class="card-header">
              <h3></h3>
              <el-button type="primary" link @click="goToTaskList"></el-button>
            </div>
          </template>
          
          <div v-if="recentTasks.length > 0">
            <div v-for="task in recentTasks" :key="task.id" class="task-item">
              <div class="task-info">
                <h4>{{ task.city }}</h4>
                <p>
                  : <el-tag :type="getStatusType(task.status)" size="small">{{ task.status }}</el-tag>
                </p>
                <p>: {{ formatDate(task.start_time) }}</p>
                <p>: {{ task.success_items }}/{{ task.total_items }}</p>
              </div>
              <div class="task-actions">
                <el-button 
                  type="primary" 
                  size="small" 
                  @click="viewTaskDetail(task)"
                  :disabled="task.status === ''"
                >
                  
                </el-button>
              </div>
            </div>
          </div>
          <el-empty v-else description="" />
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { ElMessage } from 'element-plus';
import CrawlTaskForm from '../components/CrawlTaskForm.vue';
import api from '../api/api';

export default {
  name: 'HomeView',
  
  components: {
    CrawlTaskForm
  },
  
  setup() {
    const router = useRouter();
    const loading = ref(false);
    const recentTasks = ref([]);
    
    // 
    const fetchRecentTasks = async () => {
      try {
        loading.value = true;
        
        const params = {
          limit: 5,
          offset: 0
        };
        
        recentTasks.value = await api.getTasks(params);
        loading.value = false;
      } catch (error) {
        loading.value = false;
        ElMessage.error('');
        console.error(error);
      }
    };
    
    // 
    const handleTaskCreated = (task) => {
      ElMessage.success(`${task.city}`);
      fetchRecentTasks();
    };
    
    // 
    const goToTaskList = () => {
      router.push({ name: 'taskList' });
    };
    
    // 
    const viewTaskDetail = (task) => {
      router.push({ 
        name: 'houseList', 
        query: { 
          taskId: task.id,
          city: task.city
        } 
      });
    };
    
    // 
    const getStatusType = (status) => {
      switch (status) {
        case '':
        case 'In Progress':
          return 'warning';
        case '':
        case 'Completed':
          return 'success';
        case '':
        case 'Failed':
          return 'danger';
        default:
          return 'info';
      }
    };
    
    // 
    const formatDate = (dateString) => {
      if (!dateString) return '';
      
      const date = new Date(dateString);
      return date.toLocaleString();
    };
    
    // 
    onMounted(() => {
      fetchRecentTasks();
    });
    
    return {
      loading,
      recentTasks,
      handleTaskCreated,
      goToTaskList,
      viewTaskDetail,
      getStatusType,
      formatDate
    };
  }
};
</script>

<style scoped>
.home {
  padding: 0;
}

.welcome-section {
  text-align: center;
  margin: 20px 0 30px;
}

.welcome-section h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: #409EFF;
}

.subtitle {
  font-size: 1.2rem;
  color: #606266;
  margin-bottom: 20px;
}

.intro-card {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-header h2, .card-header h3 {
  margin: 0;
}

.system-intro {
  line-height: 1.6;
}

.system-intro h3 {
  margin-top: 20px;
  margin-bottom: 15px;
}

.feature-card {
  height: 100%;
  margin-bottom: 20px;
}

.feature-header {
  display: flex;
  align-items: center;
  gap: 8px;
}

.latest-tasks {
  margin-top: 20px;
}

.task-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #EBEEF5;
}

.task-item:last-child {
  border-bottom: none;
}

.task-info h4 {
  margin: 0 0 5px 0;
}

.task-info p {
  margin: 5px 0;
  font-size: 0.9rem;
  color: #606266;
}

.task-actions {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
</style>