<template>
  <div class="analysis-results">
    <el-card>
      <template #header>
        <div class="card-header">
          <h3></h3>
          <div class="filter-actions">
            <el-select v-model="selectedCity" placeholder="" clearable @change="handleCityChange">
              <el-option
                v-for="(code, name) in cities"
                :key="name"
                :label="name"
                :value="name"
              />
            </el-select>
            <el-button type="primary" size="small" @click="refreshData"></el-button>
          </div>
        </div>
      </template>
      
      <div v-loading="loading">
        <!--  -->
        <div class="summary-section" v-if="summaryData">
          <h4> - {{ selectedCity || '' }}</h4>
          <el-row :gutter="20">
            <el-col :span="6">
              <el-card shadow="hover" class="stat-card">
                <h2>{{ summaryData.total_count }}</h2>
                <p></p>
              </el-card>
            </el-col>
            <el-col :span="6">
              <el-card shadow="hover" class="stat-card">
                <h2>{{ summaryData.avg_price }}/</h2>
                <p></p>
              </el-card>
            </el-col>
            <el-col :span="6">
              <el-card shadow="hover" class="stat-card">
                <h2>{{ summaryData.avg_unit_price }}//</h2>
                <p></p>
              </el-card>
            </el-col>
            <el-col :span="6">
              <el-card shadow="hover" class="stat-card">
                <h2>{{ getTopDistrict() }}</h2>
                <p></p>
              </el-card>
            </el-col>
          </el-row>
        </div>
        
        <!--  -->
        <el-tabs v-model="activeTab" @tab-click="handleTabClick">
          <el-tab-pane label="" name="district_analysis">
            <div class="chart-container" v-if="analysisData.district_analysis">
              <div id="district-price-chart" class="chart"></div>
              <div id="district-count-chart" class="chart"></div>
            </div>
            <el-empty v-else description="" />
          </el-tab-pane>
          
          <el-tab-pane label="" name="room_type_analysis">
            <div class="chart-container" v-if="analysisData.room_type_analysis">
              <div id="room-type-price-chart" class="chart"></div>
              <div id="room-type-count-chart" class="chart"></div>
            </div>
            <el-empty v-else description="" />
          </el-tab-pane>
          
          <el-tab-pane label="" name="direction_analysis">
            <div class="chart-container" v-if="analysisData.direction_analysis">
              <div id="direction-chart" class="chart"></div>
            </div>
            <el-empty v-else description="" />
          </el-tab-pane>
          
          <el-tab-pane label="" name="floor_analysis">
            <div class="chart-container" v-if="analysisData.floor_analysis">
              <div id="floor-chart" class="chart"></div>
            </div>
            <el-empty v-else description="" />
          </el-tab-pane>
          
          <el-tab-pane label="" name="price_distribution">
            <div class="chart-container" v-if="analysisData.price_distribution">
              <div id="price-distribution-chart" class="chart"></div>
            </div>
            <el-empty v-else description="" />
          </el-tab-pane>
          
          <el-tab-pane label="" name="community_analysis">
            <div class="chart-container" v-if="analysisData.community_analysis">
              <div id="community-chart" class="chart"></div>
            </div>
            <el-empty v-else description="" />
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-card>
  </div>
</template>

<script>
import { ref, reactive, onMounted, watch } from 'vue';
import { ElMessage } from 'element-plus';
import { useRoute } from 'vue-router';
import api from '../api/api';
import * as echarts from 'echarts';

export default {
  name: 'AnalysisResults',
  
  setup() {
    const route = useRoute();
    
    // 
    const loading = ref(false);
    
    // 
    const cities = ref({});
    const selectedCity = ref('');
    
    // 
    const activeTab = ref('district_analysis');
    
    // 
    const charts = reactive({});
    
    // 
    const analysisData = reactive({
      district_analysis: null,
      room_type_analysis: null,
      direction_analysis: null,
      floor_analysis: null,
      price_distribution: null,
      community_analysis: null
    });
    
    // 
    const summaryData = ref(null);
    
    // 
    watch(() => route.query, (query) => {
      if (query.city) {
        selectedCity.value = query.city;
        fetchData();
      }
    }, { immediate: true });
    
    // 
    const getCities = async () => {
      try {
        cities.value = await api.getCities();
      } catch (error) {
        ElMessage.error('');
        console.error(error);
      }
    };
    
    // 
    const handleCityChange = () => {
      fetchData();
    };
    
    // 
    const handleTabClick = () => {
      // DOM
      setTimeout(() => {
        renderCharts();
      }, 50);
    };
    
    // 
    const fetchData = async () => {
      try {
        loading.value = true;
        
        // 
        await fetchSummaryData();
        
        // 
        await Promise.all([
          fetchAnalysisResult('district_analysis'),
          fetchAnalysisResult('room_type_analysis'),
          fetchAnalysisResult('direction_analysis'),
          fetchAnalysisResult('floor_analysis'),
          fetchAnalysisResult('price_distribution'),
          fetchAnalysisResult('community_analysis')
        ]);
        
        loading.value = false;
        
        // 
        renderCharts();
      } catch (error) {
        loading.value = false;
        ElMessage.error('');
        console.error(error);
      }
    };
    
    // 
    const fetchSummaryData = async () => {
      try {
        const params = {};
        if (selectedCity.value) {
          params.city = selectedCity.value;
        }
        
        summaryData.value = await api.getSummaryStatistics(selectedCity.value);
      } catch (error) {
        console.error('', error);
      }
    };
    
    // 
    const fetchAnalysisResult = async (analysisType) => {
      try {
        const params = {
          analysis_type: analysisType,
          limit: 1 // 
        };
        
        if (selectedCity.value) {
          params.city = selectedCity.value;
        }
        
        const results = await api.getAnalysisResults(params);
        
        if (results && results.length > 0) {
          analysisData[analysisType] = JSON.parse(results[0].result_data);
        } else {
          analysisData[analysisType] = null;
        }
      } catch (error) {
        console.error(`${analysisType}`, error);
        analysisData[analysisType] = null;
      }
    };
    
    // 
    const refreshData = () => {
      fetchData();
    };
    
    // 
    const getTopDistrict = () => {
      if (!summaryData.value || !summaryData.value.district_distribution || summaryData.value.district_distribution.length === 0) {
        return '';
      }
      
      return summaryData.value.district_distribution[0].district;
    };
    
    // 
    const renderCharts = () => {
      switch (activeTab.value) {
        case 'district_analysis':
          renderDistrictAnalysisCharts();
          break;
        case 'room_type_analysis':
          renderRoomTypeAnalysisCharts();
          break;
        case 'direction_analysis':
          renderDirectionAnalysisChart();
          break;
        case 'floor_analysis':
          renderFloorAnalysisChart();
          break;
        case 'price_distribution':
          renderPriceDistributionChart();
          break;
        case 'community_analysis':
          renderCommunityAnalysisChart();
          break;
      }
    };
    
    // 
    const renderDistrictAnalysisCharts = () => {
      if (!analysisData.district_analysis) return;
      
      // -
      renderChart({
        elementId: 'district-price-chart',
        title: '',
        data: analysisData.district_analysis,
        xKey: 'location_qu',
        yKey: 'avg_price',
        type: 'bar'
      });
      
      // -
      renderChart({
        elementId: 'district-count-chart',
        title: '',
        data: analysisData.district_analysis,
        xKey: 'location_qu',
        yKey: 'house_count',
        type: 'bar'
      });
    };
    
    // 
    const renderRoomTypeAnalysisCharts = () => {
      if (!analysisData.room_type_analysis) return;
      
      // -
      renderChart({
        elementId: 'room-type-price-chart',
        title: '',
        data: analysisData.room_type_analysis,
        xKey: 'room_type',
        yKey: 'avg_price',
        type: 'bar'
      });
      
      // -
      renderChart({
        elementId: 'room-type-count-chart',
        title: '',
        data: analysisData.room_type_analysis,
        xKey: 'room_type',
        yKey: 'house_count',
        type: 'bar'
      });
    };
    
    // 
    const renderDirectionAnalysisChart = () => {
      if (!analysisData.direction_analysis) return;
      
      renderChart({
        elementId: 'direction-chart',
        title: '',
        data: analysisData.direction_analysis,
        xKey: 'direction',
        yKey: 'avg_price',
        type: 'bar'
      });
    };
    
    // 
    const renderFloorAnalysisChart = () => {
      if (!analysisData.floor_analysis) return;
      
      renderChart({
        elementId: 'floor-chart',
        title: '',
        data: analysisData.floor_analysis,
        xKey: 'floor_type',
        yKey: 'avg_price',
        type: 'bar'
      });
    };
    
    // 
    const renderPriceDistributionChart = () => {
      if (!analysisData.price_distribution) return;
      
      renderChart({
        elementId: 'price-distribution-chart',
        title: '',
        data: analysisData.price_distribution,
        xKey: 'price_range',
        yKey: 'house_count',
        type: 'bar'
      });
    };
    
    // 
    const renderCommunityAnalysisChart = () => {
      if (!analysisData.community_analysis) return;
      
      // 15
      const topCommunities = analysisData.community_analysis.slice(0, 15);
      
      renderChart({
        elementId: 'community-chart',
        title: '',
        data: topCommunities,
        xKey: 'location_small',
        yKey: 'avg_price',
        type: 'bar'
      });
    };
    
    // 
    const renderChart = ({ elementId, title, data, xKey, yKey, type = 'bar' }) => {
      const chartDom = document.getElementById(elementId);
      if (!chartDom) return;
      
      // 
      if (charts[elementId]) {
        charts[elementId].dispose();
      }
      
      // 
      charts[elementId] = echarts.init(chartDom);
      
      // 
      const xAxis = data.map(item => item[xKey]);
      const seriesData = data.map(item => item[yKey]);
      
      // 
      const option = {
        title: {
          text: title,
          left: 'center'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          data: xAxis,
          axisLabel: {
            interval: 0,
            rotate: 30
          }
        },
        yAxis: {
          type: 'value'
        },
        series: [
          {
            name: yKey === 'avg_price' ? '' : '',
            type: type,
            data: seriesData
          }
        ]
      };
      
      // 
      charts[elementId].setOption(option);
      
      // 
      window.addEventListener('resize', () => {
        charts[elementId].resize();
      });
    };
    
    // 
    onMounted(() => {
      getCities();
      
      // URL
      if (route.query.city) {
        selectedCity.value = route.query.city;
      }
      
      fetchData();
    });
    
    return {
      loading,
      cities,
      selectedCity,
      activeTab,
      analysisData,
      summaryData,
      handleCityChange,
      handleTabClick,
      refreshData,
      getTopDistrict
    };
  }
};
</script>

<style scoped>
.analysis-results {
  margin-bottom: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter-actions {
  display: flex;
  gap: 10px;
}

.summary-section {
  margin-bottom: 20px;
}

.stat-card {
  text-align: center;
  padding: 15px;
}

.stat-card h2 {
  font-size: 24px;
  margin: 0;
  color: #409EFF;
}

.stat-card p {
  margin: 5px 0 0;
  color: #606266;
}

.chart-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
}

.chart {
  height: 400px;
  width: 100%;
  margin-bottom: 20px;
}

@media (min-width: 992px) {
  .chart {
    width: calc(50% - 10px);
  }
}
</style> 